.footer1 {
  background-color: #07043e;
  color: #fff;
  padding: 20px;
  position: absolute;
  bottom: 1;
  width: 96%;
  overflow: hidden;
  overflow-y: hidden;
  overflow-x: hidden;
}

.container1 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  
}

.social1 {
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  overflow-y: hidden;
  overflow-x: hidden;
}

.social1 a {
  color: #fff;
  margin-right: 20px;
  overflow: hidden;
  overflow-y: hidden;
  overflow-x: hidden;
}

.content1 {
  text-align: center;
  overflow: hidden;
  overflow-y: hidden;
  overflow-x: hidden;
}
