@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap");



body {
  background-color: rgb(248, 246, 253);
}

body,
input,
textarea,
button {
  font-family: "Inter", sans-serif;
}



/* Contact.js */
.form {
  height: 79vh;
  width: 50vh;
  margin-left: 80vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.shivam{
  margin: 0;
  padding: 0;

}


@media screen and (max-width: 500px ) {
    .form {
        height: 80vh;
        width: 50vh;
        margin-left: 2vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
      }
    
  }

  @media only screen and (min-width: px) {

    .form {
        height: 80vh;
        width: 50vh;
        margin-left: 30vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
      }



  }

.form > h1 {
  color: blue;
  margin-bottom: 30px;
}

.form > input,
textarea {
  padding: 20px;
  border-radius: 3px;
  /* box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.137); */
  margin-bottom: 20px;
  border: 1px solid rgb(230, 18, 18);
  /* border: none; */
  background: #e8cbcb;
  font-size: 16px;
  color: rgb(18, 18, 209);
  outline: none;
}

.form > input:focus,
textarea:focus {
  border: 1px solid rgb(0, 0, 196);
}

.form > textarea {
  height: 150px;
  max-width: 400px;
  max-height: 50px;
}

.form > label {
  padding-bottom: 10px;
  color: rgb(11, 11, 231);
  font-weight: bold;
}

.form > button {
  padding: 20px;
  border: none;
  background-color: rgb(2, 2, 110);
  font-weight: 500;
  font-size: 20px;
  border-radius: 3px;
  color: #fff;
  cursor: pointer;
  transition: 0.2s ease-in-out;
  margin-top: 19px;
}

.form > button:hover {
  background-color: rgb(0, 0, 196);
}